$primaryBG: #2a3438;
$seconDaryBG: #352f44;
$activeBG: #5c5470;
$hoverBG: #dbd8e3;

.sheet {
  &.profile-section {
    //background-color: #f1f5f5;
    .content {
      display: flex;
      margin-top: 5%;
      align-items: center;
      justify-content: space-around;

      .webDevelopementLeft {
        display: flex;
        justify-content: flex-start;
        flex-grow: 1;
        img {
          height: 200px;
          width: 200px;
        }
      }
      .profile-description {
        text-align: center;
        h4 {
          font-family: "Readex Pro";
          font-weight: 100;
        }
        h2 {
          font-family: "Gwendolyn";
          font-size: 32px;
        }
        p {
          padding-top: 5%;
          font-size: 1.2rem;
          text-align: justify;
        }
      }
      .webDevelopementRight {
        display: flex;
        justify-content: flex-end;
        flex-grow: 1;
        img {
          height: 200px;
          width: 200px;
        }
      }
    }
  }

  &.skills {
    background: #ffffff;
    box-shadow: 0px 0px 3px 2px #c1c1c1;
    border-radius: 32px;
    margin: 0 0 5vh 0;
    h3 {
      text-align: center;
    }
    .content {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
      gap: 2rem;
      transition: box-shadow 0.9s linear;
      justify-content: space-around;
      margin: 18vh;
      .card {
        background: $seconDaryBG;
        text-align: center;
        padding: 0;
        margin-bottom: 16px;
        border-color: $seconDaryBG;
        border: unset;
        box-shadow: 0px 0px 3px 2px #c1c1c1;

        .card-header {
          margin: auto;
          flex-direction: row;
          justify-content: stretch;
          background: #fff;
          color: #000;
          border: 1px $seconDaryBG solid;
          border-radius: 8px;
          //border-radius: 7px 7px 0 0;
          transform: translate(0, -50%);
          .cardHeaderText {
            font-size: 16px;
          }
        }
        .card-content {
          display: flex;
          flex-flow: column;
          gap: 1rem;
          padding: 1rem;
          background: $seconDaryBG;
          color: #fff;
          border-radius: 0 0 8px 8px;

          .expertise {
            display: flex;
            flex-flow: row;
            gap: 1rem;
            align-items: center;
            justify-content: center;
          }
          span {
            display: flex;
            background: black;
            width: 30px;
            height: 30px;
            align-items: center;
            justify-content: center;
            border-radius: 16px;
          }
        }
        &:hover {
          box-shadow: 0px 0px 3px 2px #4fc911;
          cursor: pointer;
          border-color: #4fc911;
        }
      }
    }
  }

  &.experiences {
    color: white;
    background: $seconDaryBG;
    box-shadow: 0px 0px 3px 2px #c1c1c1;
    //border-radius: 32px;
    margin: 0 0 5vh 0;
    h3 {
      color: #ffffff;
      text-align: center;
    }

    .content {
      margin: 18vh;
      .accordian {
        margin: 16px;
        .title {
          display: flex;
          flex-direction: row;
          align-content: center;
          justify-content: space-around;
          height: 9vh;
          background: $activeBG;
        }
        .content {
          color: black;
        }
      }
    }
    .experience {
      height: 9vh;
      background: #ffffff;
      display: flex;
      align-content: center;
      justify-content: space-around;
      margin: 1rem;
      span {
        display: flex;
        align-items: center;
      }
    }
  }
}

#root {
  background: linear-gradient(
    -185deg,
    #ffffff 28%,
    $primaryBG 28%,
    $primaryBG 62%,
    #ffffff 62%
  );
}

@media screen and (min-width: 601px) and (max-width: 992px) {
  .sheet.skills .content {
    grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
    margin: 0;
  }
  .sheet.skills h3 {
    margin-bottom: 5vh;
  }
}

/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
    display: block;
    .logoAndMenu {
      display: flex;
      align-items: center;
    }
    .right-side-elements {
      display: block;
      float: none;
    }
  }
  .topnav.responsive div {
    float: none;
    display: block;
    text-align: left;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  .topnav div:not(:first-child) {
    display: none;
  }
  .topnav .icon {
    position: absolute;
    right: 16px;
    float: right;
    display: block;
  }
  .topnav.responsive input {
    float: none;
    display: block;
    text-align: left;
  }

  .sheet.skills .content {
    grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
    margin: inherit;
  }
  .sheet.skills h3 {
    margin-bottom: 5vh;
  }
}
